import {
	hotCar,
	carType,
	carIcon
} from "../../../api/user.js"
import carStyle from "../../../components/car-style/car-style.vue"
import {
	mapMutations
} from "vuex"
export default {
	data() {
		return {
			hotBrandList: [],
			brandList: [],
			keys: [],
			carIcon: carIcon,
			curView: 'carA',
			brandId: '',
			showBrandStyle: false //显示车系款式
		}
	},
	components: {
		carStyle
	},
	onLoad() {
		uni.hideShareMenu()

		this.getCarType()
		this.getHotCat()
	},
	onReady() {

	},
	methods: {
		...mapMutations(['setCarInfo']),
		hideCarStyle() {
			this.showBrandStyle = false;
			this.brandId = ''
		},
		// 获取热门车
		getHotCat() {
			hotCar().then(r => {
				if (r.data.code == 0) {
					this.hotBrandList = r.data.result.brands
				}
			})
		},
		// 获取所有车系
		getCarType() {
			uni.showLoading({
				title: '加载中...'
			})
			carType().then(r => {
				uni.hideLoading()
				if (r.data.code == 0) {
					this.fitData(r.data.result.brands)
				}
			})
		},
		// //适配品牌数据
		fitData(data) {
			let keys = data.map((item) => {
				return item.first_letter
			})
			this.keys = Array.from(new Set(keys)).sort()
			let arr = [];
			for (let key of this.keys) {
				let obj = {}
				obj.key = key
				obj.brandList = []
				for (let i in data) {
					if (key == data[i].first_letter) {
						obj.brandList.push(data[i])
					}
				}
				arr.push(obj)
			}
			this.brandList = arr
		},
		// 滚动到指定的位置
		jumpIndex(key) {
			this.curView = 'car' + key
		},
		// 打开车系
		openBrandStyle(id, name) {
			this.setCarInfo({
				brand_name: name
			})
			this.brandId = id;
			this.showBrandStyle = true
		}
	}
}
